<template lang="pug">
section.detail_main
    .header.border-1px 
        .left
            router-link(to="/")
                img(src="../../assets/icon/arrow_left.png")
        navbar.nav(:navs="navs", :chooseItem="chooseItem")
        .right
            .love.right-icon(@click="clickLove")
                img(:src="loveSrc")
            .share.right-icon
                img(src="../../assets/icon/share.png")
    v-touch(@swipeleft="onSwipeLeft", @swiperight="onSwipeRight", :priority="1")
        transition(name="transitionName")
            keep-alive
                router-view.Router
</template>

<script>
import { Toast } from 'mint-ui'
import navbar from '@/components/NavBar.vue'
export default {
  data () {
    return {
        navs: ['商品','详情','评价'],
        chooseItem: 1,
        loveSrc: 'static/icon/star.png',
        isCollected: false,
        ballShow: false,
        transitionName: 'scroll-left'
    }
  },
  components: {
      navbar
  },
  created() {
      if(this.$store.state.tabHidden) {
          this.$store.dispatch('tabHidden')
      }
  },
  computed: {
      getNavState() {
          return this.$store.state.navState;
      }
  },
  watch: {
      getNavState(value) {
          switch(value) {
                case 0: 
                    this.$router.push('/product/info')
                    break;
                case 1: 
                    this.$router.push('/product/detail')
                    break;
                case 2: 
                    this.$router.push('/product/comment')
                    break;
                default: 
                    this.$router.push('/product/info')
            
          }
      },
      '$route' (to, from) {
          if(this.$store.state.tabHidden) {
              this.$store.dispatch('tabHidden')
          }
          let toPath = to.path.split('/')[2].length
          let fromPath = from.path.split('/')[2].length
          toPath > fromPath ? this.transitionName="scroll-left": this.transitionName = "scroll-right"
      }
  },
  methods: {
      clickLove() {
          if(!this.isCollected) {
              this.loveSrc = "static/icon/starChoose.png"
              Toast({
                  message: '收藏成功',
                  position: 'middle',
                  duration: 1000
              })
          } else {
              this.loveSrc = "static/icon/star.png"
              Toast({
                  message: '取消收藏',
                  position: 'middle',
                  duration: 1000
              })
          }
          this.isCollected = !this.isCollected;
      },
      onSwipeLeft() {
          if(this.$store.state.tabHidden) {
              this.$store.dispatch('tabHidden')
          }
          let index = "";
          if(this.$route.name !== null) {
              index = this.$route.name.slice(7)
              switch(index) {
                case 'Info':
                    this.$router.push('/product/detail')
                    this.chooseItem = 2
                    break;
                case 'Detail':
                    this.$router.push('/product/comment')
                    this.chooseItem = 3
                    break;
                default: break;
              }
          }
      },
      onSwipeRight() {
          if(this.$store.state.tabHidden) {
              this.$store.dispatch('tabHidden')
          }
          let index = "";
          if(this.$route.name !== null) {
              index = this.$route.name.slice(7)
              switch(index) {
                case 'Comment':
                    this.$router.push('/product/detail')
                    this.chooseItem = 2
                    break;
                case 'Detail':
                    this.$router.push('/product/info')
                    this.chooseItem = 1
                    break;
                default: break;
              }
          }
      },
      beforeDrop() {

      }, 
      dropping() {

      },
      afterDrop() {

      },
      onSwipeRight() {
          
      }
  }
}
</script>

<style lang="stylus" type="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
.detail_main
  .header
    position fixed
    top 0
    left 0
    right 0
    height 1.066667rem
    display flex
    align-items center
    border-1px()
    padding .1rem 0rem .05rem
    .left
      width .8rem
      height .8rem
      flex 2
      img
        height 100%
    .nav
      flex 3
      display inline-block
    .right
      flex 2.2
      .right-icon
        width .8rem
        height .8rem
        display inline-block
        &:first-child
          padding-right .3rem
        img
          width 100%
  .Router
    transition all .4s ease
    &.scroll-left-enter, &.scroll-right-leave-active
      opacity 0
      transform translateX(100%)
    &.scroll-left-leave-active, &.scroll-right-enter
      opacity 0
      transform translate(-100%, 0)
  .ball-container
    position absolute
    left 19%
    bottom 2%   
    z-index 10
    .ball   
      transition all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41)
      height 1rem
      width 1rem
      .ball-img 
        width 100%
        transition: all 0.4s linear
</style>




